.modal {
    width: 90%;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    position: fixed;
    top: 20vh;
    left: 5%;
    z-index: 100;
}

.modal__content {
    padding: 2rem 0.5rem;
}

.modal__header {
    padding: 1rem;
    background: rgb(28, 56, 214);
    color: peachpuff;
}

.modal__actions {
    display: flex;
    justify-content: flex-end;
    padding-bottom: 1rem;
}

@media (min-width: 768px) {
    .modal {
        width: 50rem;
        left: calc((100% - 50rem) / 2);
    }
}